/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.uk-card-footer {
  border-top: none !important;
}

html {
  font-size: 62.5%;
}

body {
  font-family: Hiragino Sans,Hiragino Kaku Gothic ProN,Meiryo,sans-serif;
  line-height: 1.8;
  color: #5f5e5e;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-feature-settings: "palt";
  font-feature-settings: "palt";
  font-size: 10px;
  font-size: 1.0rem;
  background-image: url(../img/bg.png);
  background-position: left top;
  background-repeat: repeat;
  background-color: #f4f4f4;
  letter-spacing: 0.05rem;
  text-align: justify;
  text-justify: inter-ideograph;
}

a {
  text-decoration: none;
  color: #5f5e5e;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transition-timing-function: linear;
  transition-timing-function: linear;
}

a:hover {
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transition-timing-function: linear;
  transition-timing-function: linear;
  color: #5f5e5e;
  opacity: 0.5;
  text-decoration: none;
}

.uk-button-primary {
  background-color: #EEE;
  color: #5f5e5e;
  font-weight: 600;
  border-radius: .2rem;
}

.uk-button-primary:hover {
  color: #5f5e5e;
  background-color: rgba(204, 204, 204, 0.8);
}

.uk-button-large {
  padding: 0 40px;
  line-height: 43px;
  font-size: 1rem;
}

#header .uk-navbar-container {
  height: 63px;
}

#header .uk-navbar {
  align-items: center;
  background-color: #FFF;
  padding: 0 64px;
}

#header .logo {
  outline: none;
}

#header .logo .st0 {
  fill: #5f5e5e;
}

#header .logo svg {
  height: 14px;
  outline: none;
  display: inline-block;
}

@media screen and (max-width: 1140px) {
  #header .logo {
    text-align: center;
  }
  #header .logo svg {
    height: 14px;
  }
}

#header .navbar-collapse.show {
  padding-bottom: 3rem;
}

#header .uk-navbar-nav {
  letter-spacing: .1em;
  align-items: center;
}

#header .uk-navbar-nav li.nav > a {
  display: inline-block;
  min-height: 0;
  font-size: 1.4rem;
  font-weight: 600;
  font-style: normal;
  color: #000;
  padding-left: 1.5em;
  padding-right: 1.5em;
}

#header .uk-navbar-nav li.nav > a .icon {
  transform: translateY(-2px);
  margin-left: 4px;
  width: 15px;
}

#header .contact {
  margin-left: 1.5rem;
}

#header .contact a {
  display: flex;
  align-items: center;
  color: #FFF;
  background-color: #666;
  font-size: 1.2rem;
  line-height: 1;
  font-weight: 600;
  min-height: 0;
  padding-top: .4em;
  padding-bottom: .4em;
  border-radius: 20px;
}

#header .contact a .icon {
  position: relative;
  top: 1px;
  margin-right: .4em;
}

#header .contact a .icon svg {
  width: 15px;
}

.uk-navbar-dropdown {
  border: none;
  background-color: #666;
  padding: .5em 1.5rem;
  box-shadow: none;
}

.uk-navbar-dropdown .uk-navbar-dropdown-nav {
  border-radius: 0;
  text-align: left;
}

.uk-navbar-dropdown .uk-navbar-dropdown-nav a {
  color: #5f5e5e;
  display: inline-block;
  color: #FFF;
  font-size: 1.3rem;
  font-weight: 600;
  text-align: center;
}

.uk-navbar-dropdown .uk-navbar-dropdown-nav a:hover {
  color: #ddd;
}

.navbar-toggler {
  padding: 0;
  border: none;
}

.navbar-toggler button {
  outline: none;
}

.navbar-toggler ion-icon {
  font-size: 2rem;
  line-height: 1;
}

#footer {
  border-top: 1px solid #ebebeb;
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-align: center;
  font-size: .8rem;
}

.section {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.section h1 {
  text-align: center;
  font-size: 2.4rem;
  font-weight: 900;
  margin-bottom: 5rem;
  line-height: 1;
}

.section h2 {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 900;
  margin-bottom: 5rem;
  line-height: 1;
}

.section h3 {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 1em;
  line-height: 1;
}

.section > p {
  text-align: center;
}

@media screen and (max-width: 720px) {
  .section {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
  .section h1 {
    font-size: 1.5rem;
    margin-bottom: 3rem;
  }
  .section h3 {
    font-size: 1rem;
  }
}

#main-cover {
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

@media screen and (max-width: 720px) {
  #main-cover {
    padding-top: 67%;
  }
}

#main-cover:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
}

#main-cover h1 {
  position: relative;
  z-index: 2;
  font-size: 1.8rem;
  font-weight: 900;
  text-align: center;
  color: #FFF;
}

@media screen and (max-width: 720px) {
  #main-cover h1 {
    font-size: 1rem;
  }
}

.home-section h2 {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 900;
  margin-bottom: 5rem;
  line-height: 1;
}

.home-section h3 {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 1em;
  line-height: 1;
}

@media screen and (max-width: 720px) {
  .home-section {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
  .home-section h2 {
    font-size: 1.5rem;
    margin-bottom: 3rem;
  }
  .home-section h3 {
    font-size: 1rem;
  }
}

#home-about {
  position: relative;
  overflow: hidden;
  background-image: url(../img/bg_white.png);
  background-repeat: no-repeat;
  background-size: cover;
}

#home-about h3 {
  position: relative;
  z-index: 2;
  text-align: center;
  line-height: 1;
}

#home-about .about {
  margin-bottom: 5rem;
  position: relative;
}

#home-about .about .uk-cover-container {
  margin-left: auto;
  margin-right: auto;
  border-radius: 100%;
  overflow: hidden;
  width: 220px;
  height: 220px;
  margin-bottom: 1rem;
}

#home-about .about .about-content {
  margin-left: auto;
  margin-right: auto;
  max-width: 350px;
}

#home-about .about .more {
  margin-top: 2rem;
  text-align: center;
}

#program-container {
  position: relative;
  overflow: hidden;
}

.personal-container h2 {
  text-align: center;
  font-weight: 900;
  font-size: 2.2rem;
  line-height: 1;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.personal {
  padding-top: 30%;
  padding-bottom: 5%;
  box-sizing: border-box;
}

.personal .inner {
  position: relative;
  z-index: 2;
}

.personal .inner p {
  margin-bottom: 2rem;
}

.personal .inner p:last-of-type {
  margin-bottom: 0;
}

@media screen and (max-width: 720px) {
  .personal h2 {
    font-size: 1.4rem;
    margin-bottom: 1rem;
  }
  .personal .inner p {
    margin-bottom: 1rem;
  }
}
